.section {
    &-questionnare {}

    &-subcta {
        .section-bg {
            .imgtobg-o {
                @include absoldefault;
            }
        }
    }
}

.herobanner {
    position: relative;
    height: 85rem;
    min-height: 100vh;
    overflow: hidden;
    @include unselect;

    &-bg,
    &-shape,
    &-cont,
    &-phone {
        position: absolute;
    }

    &-bg {
        @include absoldefault;

        img {
            @include imgfit;
        }
    }

    &-shape {
        position: absolute;
        top: 0;
        right: 0;
        width: 65vw;

        img {
            width: 100%;
            height: 100%;
        }
    }

    &-cont {
        z-index: 4;
        position: absolute;
        bottom: 10%;
        left: 0;
        width: 100%;

        &-inner {
            max-width: 45%;
        }

        .heading {
            font-size: 6.75rem;
            margin-bottom: .5rem;

            &-sub {
                font-size: 2.25rem;
                font-weight: 300;
                margin-bottom: 2.5rem;
                padding: 1rem 2rem;
                color: #fff;
                background-color: $color-main;
                display: inline-block;
                border-radius: 25px;
            }

            @media (max-width: 1600px) {
                font-size: 5.5rem;
            }
        }

        .store-btns {
            margin-top: 4.5rem;

            a {
                box-shadow: 0 4px 15px rgba(#000, .15);

                &:not(:last-child) {
                    margin-right: 1rem;
                }
            }
        }
    }

    &-phone {
        z-index: 5;
        position: absolute;
        bottom: 5%;
        right: 30%;
        transform: rotate(20deg);
        transform-origin: 100% 100%;
        box-shadow: -5px 5px 25px -5px rgba(#000, 0.3);
        border-radius: 55px;

        &-container,
        &-gif {}

        &-container {
            z-index: 1;
            position: relative;
            width: 100%;
            width: 32rem;
        }

        &-gif {
            z-index: 0;
            @include absolcenter;
            width: calc(100% - 40px);
            height: calc(100% - 30px);
        }
    }
}

.abouthp {
    position: relative;

    &-box {
        position: relative;
        color: #fff;
        background-color: $color-main;
        padding: 96px 120px;
        max-width: 1400px;
        @include margin-auto;

        *::-moz-selection,
        *::selection {
            color: #fff;
            background-color: $color-sub;
        }

        &:not(.heading) {
            margin-left: 96px;
        }

        .heading {
            font-size: 8.5rem;
            margin-bottom: 5rem;
        }

        p {
            font-size: 2rem;
            line-height: 3.5rem;
            max-width: 50%;
        }

        @media (max-width: 1600px) {
            .heading {
                font-size: 7rem;
            }
        }

        @media (max-width: 1400px) {
            .heading {
                font-size: 4rem;
            }
        }
    }

    &-vid {
        width: 700px;
        padding-bottom: 56.25%;

        &-box {
            position: absolute;
            bottom: -48px;
            right: -108px;
        }

        &-link {
            @include absoldefault;

            &::after {
                content: "";

            }
        }

        &-img {
            @include imgfit;
        }

        &-playbtn {
            @include absolcenter;
            width: 70px;
            height: 70px;

            svg {
                width: 100%;
                height: 100%;

                path {
                    transition: all .3s ease;
                }
            }
        }

        &:hover svg path {
            fill: $color-sub;
        }

        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        @media (max-width: 1600px) {
            width: 600px;
        }
    }
}

.appshowcase {
    position: relative;
    max-width: 1140px;
    @include margin-auto;
    padding-bottom: 5rem;

    .section-body {
        position: relative;

    }

    .swiper-container {
        width: 100%;
        padding: 8rem 0;
        margin-top: -8rem;
    }

    .swiper-slide {
        background-position: center;
        background-size: cover;
        width: 300px;
        height: 635px;
        border-radius: 55px;
        overflow: hidden;
        transition: all .2s ease-out;

        &::before {
            content: "";
            @include absoldefault;
            background-image: url('data:image/png;base64,');
            background-size: 100% 100%;
            opacity: 0;
            transition: all .2s ease-out
        }

        img {
            @include imgfit;
            transition: all .2s ease-out;
        }

        &.swiper-slide-active {
            border-radius: 55px;

            &::before {
                opacity: 1;
            }

            img {
                padding: 1.5rem 2rem;
            }
        }

    }

    .swiper-pagination {
        .swiper-pagination-bullet {
            width: 25px;
            height: 5px;
            border-radius: 8px
        }
    }
}

.questionnaire {
    z-index: 1;
    position: relative;

    &-border {
        z-index: 1;
        @include absolcenter;
        width: 90%;
        height: 100%;

        &::before,
        &::after {
            z-index: -1;
            content: "";
            @include absolcenter;
            width: 100%;
            height: 100%;
        }

        &::before {
            background: linear-gradient(to bottom right, $color-sub 5%, $color-main 50%);
        }

        &::after {
            background-color: #fff;
            width: calc(100% - 30px);
            height: calc(100% - 30px);
        }
    }

    &-cont {
        z-index: 1;
        position: relative;
        padding: 10rem;

        &-lt {
            padding: 5rem 0;
            background-color: #fff;

            .link-style-passby {
                margin-top: 2rem;
            }

            @media (min-width: 1200px) {
                margin-left: -100px
            }
        }

    }

    .heading {
        font-size: 7rem;

        @media (max-width: 1600px) {
            font-size: 5.25rem
        }
    }

    .content-wrapper p {
        @media (max-width: 1400px) {
            font-size: 2rem;
            line-height: 35px;
        }
    }
}

.autopilot {
    position: relative;
    max-width: 670px;

    &-dots {
        position: absolute;
        top: 0;
        left: 0;
        width: 200px;
        height: 200px;
        transition: all .3s ease-out;
    }

    &-dot {
        z-index: 1;
        position: absolute;
        top: 0;
        left: 0px;
        width: 100%;
        height: 100%;
        background-color: $color-main;
        border-radius: 50%;
        transition: all .5s;

        &::before {
            z-index: 1;
            content: "";
            position: absolute;
            top: 6px;
            left: 4px;
            width: 12px;
            height: 6px;
            border: 2px solid #fff;
            border-width: 0 0 2px 2px;
            opacity: 0;
            transition: all .2s ease-in;
        }
    }

    &.dotpush .autopilot-dots {
        left: -10px;
        width: 20px;
        height: 20px;
        top: 100px;
    }

    &.dotpush1 .autopilot-dots {
        left: -10px;
        top: 230px;
    }

    &.dotpush2 .autopilot-dot-1 {
        top: 25px;
    }

    &.dotpush2 .autopilot-dot-2 {
        top: 135px;
    }

    &.dotpush2 .autopilot-dot-3 {
        top: 245px;
    }

    &.dotpush2 .autopilot-dot-4 {
        top: 355px;
    }

    &.dotpush2 .autopilot-dot-5 {
        top: 465px;
    }

    &.dotpush3 .autopilot-dot {
        left: 65px;
    }

    &.dotpush4 .autopilot-dot {
        background-color: $color-sub;
    }

    &.dotpush4 .autopilot-dot::before {
        opacity: 1;
        transform: rotate(-45deg);
    }

    &-source {
        z-index: 1;
        position: relative;
        text-align: center;
        margin-bottom: 30px;

        &::before {
            z-index: -1;
            content: "";
            position: absolute;
            top: 100px;
            left: 0;
            width: 120px;
            height: 135px;
            border: 1px solid rgba(#000, .2);
            border-width: 1px 0 0 1px;
        }

        .logowrap {
            width: 200px;
            height: 200px;
            padding: 10px;
            margin-bottom: 50px;

            img {
                max-width: 100%;
                height: 100%;
            }
        }
    }

    &-dest {
        position: relative;
        list-style-type: none;
        padding-left: 0;

        &::before {
            content: "";
            position: absolute;
            top: -15px;
            left: 0;
            width: 1px;
            height: calc(100% - 60px);
            background-color: rgba(#000, .2);
        }

        li {
            position: relative;
            font-size: 2rem;
            min-height: 90px;
            padding-left: 10rem;
            margin-bottom: 2rem;
            transition: all .2s;

            span {
                // opacity: 0;
                transform: translateX(20px);
                transition: all .2s;
            }

            &::before {
                content: "";
                position: absolute;
                top: 15px;
                left: 0;
                width: 70px;
                height: 1px;
                background-color: rgba(#000, .2);
            }
        }

        @media (max-width: 1400px) {
            li {
                font-size: 1.6rem
            }
        }
    }

    &.dotpush4 .autopilot-dest li span {
        opacity: 1;
        transform: translateX(0);
    }
}

.subcta {
    .heading {
        font-size: 3.5rem
    }

    &-form {
        position: relative;
        width: 100%;
        height: 60px;
        max-width: 700px;
        @include margin-auto;
        border-radius: 225px;
        border: 1px solid rgba(#000, .1);
        box-shadow: 0 4px 15px 2px rgba(#000, .1);
        overflow: hidden;

        input:not([type="submit"]) {
            width: 100%;
            height: 100%;
            padding: 10px 150px 10px 25px;
            border-color: transparent;

            &:focus {
                outline: none;
            }
        }

        button[type="submit"] {
            width: 140px;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            border: 0;
            color: #fff;
            background-color: $color-sub;
            font-family: $font-title;
            font-weight: 600;
            text-align: center;
            text-transform: uppercase;
            cursor: pointer;
            transition: all .2s ease;

            &:hover {
                color: #fff;
                background-color: $color-main;
            }
        }
    }
}